import Vue from 'vue'
import Router from 'vue-router'
import Vote from '@/components/Vote'
import { publicPath } from '../../vue.config'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: publicPath,
      name: 'Vote',
      component: Vote
    }
  ]
})



